import React, { useState } from 'react';
import { Link } from "react-router-dom";
import 'react-slideshow-image/dist/styles.css'
import classnames from "classnames";

const AddNewCase = ({ navigation, route }) => {
    const getDetails = (types) => {
        if (route.params) {
            switch (types) {
                case "title":
                    return route.params.title
                case "type":
                    return route.params.type
                case "code":
                    return route.params.code
                case "client":
                    return route.params.client
                case "court":
                    return route.params.court
                case "hearingDate":
                    return route.params.hearingDate
                case "description":
                    return route.params.description
                default :
                    return null
            }
        }
        return ""
    }

    const [title, setTitle] = useState(getDetails("title"))
    const [type, setType] = useState(getDetails("type"))
    const [code, setCode] = useState(getDetails("code"))
    const [client, setClient] = useState(getDetails("client"))
    const [court, setCourt] = useState(getDetails("court"))
    const [hearingDate, setHearingDate] = useState(getDetails("hearingDate"))
    const [description, setDescription] = useState(getDetails("description"))
    const submitData = () => {
        console.log("In submit data");
        if (title === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Name`)
        } else if (type === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Email`)
        } else if (code === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Password`)
        } else if (client === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Email`)
        } else if (court === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Password`)
        } else if (hearingDate === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Email`)
        } else if (description === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Password`)
        } else {
            fetch("http://localhost:5000/api/cases/send-Case", {
                method: "post",
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    title,
                    type,
                    code,
                    client,
                    court,
                    hearingDate,
                    description,
                })
            })
                .then(res => res.json())
                .then(data => {
                    //                   Alert.alert(`${data.name} is saved successfuly`)
                    navigation.navigate("Login")
                })
                .catch(err => {
                    //                    Alert.alert("someting went wrong")
                })
        }
    }
    
    const { errors } = this.state;
    return (
        <div className="mid container">
            <nav className="navbar navbar-expand-sm navbar-dark bg-dark mb-4">
                <div className="container">
                    <Link to="/Lawyers" className="btn btn-lg text-white bg-dark">
                        Lawyers
                    </Link>
                    <Link to="/Cases" className="btn btn-lg text-white bg-dark">
                        Cases
                    </Link>
                    <Link to="/Chat" className="btn btn-lg text-white bg-dark">
                        Chat
                    </Link>
                    <Link to="/Payments" className="btn btn-lg text-white bg-dark">
                        Payments
                    </Link>
                    <Link to="/Dictionary" className="btn btn-lg text-white bg-dark">
                        Dictionary
                    </Link>
                    <Link to="/Reviews" className="btn btn-lg text-white bg-dark">
                        Reviews
                    </Link>
                    <button
                        className="navbar-toggler"
                        type="button"
                        data-toggle="collapse"
                        data-target="#mobile-nav"
                    >
                        <span className="navbar-toggler-icon" />
                    </button>
                </div>
            </nav>
            <div className="register mid container">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <h1 className="display-4 text-center">Add Case</h1>
                            <p className="lead text-center">You can add unlimited cases here!</p>
                            <form noValidate onSubmit={submitData()}>
                                <div className="form-group">
                                    <input
                                        type="text"
                                        className={classnames("form-control form-control-lg", {
                                            "is-invalid": errors.title
                                        })}
                                        placeholder="title"
                                        name="title"
//                                        value={this.state.title}
                                        onChange={text => setTitle(text)}
                                        
                                    />
                                    {errors.title && (
                                        <div className="invalid-feedback">{errors.title}</div>
                                    )}
                                </div>
                                <div className="form-group">
                                    <input
                                        type="text"
                                        className={classnames("form-control form-control-lg", {
                                            "is-invalid": errors.type
                                        })}
                                        placeholder="type"
                                        name="type"
//                                        value={this.state.type}
                                        onChange={text => setType(text)}
                                    />
                                    {errors.type && (
                                        <div className="invalid-feedback">{errors.type}</div>
                                    )}
                                </div>
                                <div className="form-group">
                                    <input
                                        type="text"
                                        className={classnames("form-control form-control-lg", {
                                            "is-invalid": errors.code
                                        })}
                                        placeholder="code"
                                        name="code"
//                                        value={this.state.code}
                                        onChange={text => setCode(text)}
                                    />
                                    {errors.code && (
                                        <div className="invalid-feedback">{errors.code}</div>
                                    )}
                                </div>

                                <div className="form-group">
                                    <input
                                        type="text"
                                        className={classnames("form-control form-control-lg", {
                                            "is-invalid": errors.client
                                        })}
                                        placeholder="client"
                                        name="client"
//                                        value={this.state.client}
                                        onChange={text => setClient(text)}
                                    />
                                    {errors.client && (
                                        <div className="invalid-feedback">{errors.client}</div>
                                    )}
                                </div>
                                <div className="form-group">
                                    <input
                                        type="text"
                                        className={classnames("form-control form-control-lg", {
                                            "is-invalid": errors.court
                                        })}
                                        placeholder="court"
                                        name="court"
//                                        value={this.state.court}
                                        onChange={text => setCourt(text)}
                                    />
                                    {errors.court && (
                                        <div className="invalid-feedback">{errors.court}</div>
                                    )}
                                </div>
                                <div className="form-group">
                                    <input
                                        type="text"
                                        className={classnames("form-control form-control-lg", {
                                            "is-invalid": errors.hearingDate
                                        })}
                                        placeholder="hearingDate"
                                        name="hearingDate"
//                                        value={this.state.hearingDate}
                                        onChange={text => setHearingDate(text)}
                                    />
                                    {errors.hearingDate && (
                                        <div className="invalid-feedback">{errors.hearingDate}</div>
                                    )}
                                </div>
                                <div className="form-group">
                                    <input
                                        type="text"
                                        className={classnames("form-control form-control-lg", {
                                            "is-invalid": errors.description
                                        })}
                                        placeholder="description"
                                        name="description"
//                                        value={this.state.description}
                                        onChange={text => setDescription(text)}                                        
                                        />
                                    {errors.description && (
                                        <div className="invalid-feedback">{errors.description}</div>
                                    )}
                                </div>
                                <input type="submit" className="btn btn-info btn-block mt-4" />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AddNewCase


