import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { addreview } from "../../actions/authActions";
import { Link } from "react-router-dom";
import 'react-slideshow-image/dist/styles.css'
import classnames from "classnames";
import Select from 'react-select'
import StarRatings from 'react-star-ratings';

const options = [
  { value: 'chocolate', label: 'Chocolate' },
  { value: 'strawberry', label: 'Strawberry' },
  { value: 'vanilla', label: 'Vanilla' }
]
class Dashboard extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      lawyer: "",
      email: "",
      title: "",
      description: "",
      errors: {}
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onSubmit(e) {
    e.preventDefault();

    const newLawyer = {
      name: this.state.name,
      lawyer: this.state.lawyer,
      email: this.state.email,
      title: this.state.title,
      description: this.state.description,
    };

    this.props.addreview(newLawyer, this.props.history);
  }
  changeRating(newRating, name) {
    this.setState({
      rating: newRating
    });
  }
  render() {
    //    const { user } = this.props.auth;
    const { errors } = this.state;
    return (
      <div className="mid container">
        <nav className="navbar navbar-expand-sm navbar-dark bg-dark mb-4">
          <div className="container">
            <Link to="/Lawyers" className="btn btn-lg text-white bg-dark">
              Lawyers
            </Link>
            <Link to="/Cases" className="btn btn-lg text-white bg-dark">
              Cases
            </Link>
            <Link to="/Chat" className="btn btn-lg text-white bg-dark">
              Chat
            </Link>
            <Link to="/Payments" className="btn btn-lg text-white bg-dark">
              Payments
            </Link>
            <Link to="/Dictionary" className="btn btn-lg text-white bg-dark">
              Dictionary
            </Link>
            <Link to="/Reviews" className="btn btn-lg text-white bg-dark">
              Reviews
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#mobile-nav"
            >
              <span className="navbar-toggler-icon" />
            </button>
          </div>
        </nav>

        <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '2rem' }}>
          <div className="register mid container">
            <div className="container">
              <div className="row">
                <div className="col-md-8 m-auto">
                  <h1 className="display-4 text-center">Add Review</h1>
                  <p className="lead text-center">You can only add reviews about your case's lawyers</p>
                  <form noValidate onSubmit={this.onSubmit}>
                    <div className="form-group">
                      <input
                        type="text"
                        className={classnames("form-control form-control-lg", {
                          "is-invalid": errors.name
                        })}
                        placeholder="Name"
                        name="name"
                        value={this.state.name}
                        onChange={this.onChange}
                      />
                      {errors.name && (
                        <div className="invalid-feedback">{errors.name}</div>
                      )}
                    </div>
                    <Select
                      placeholder="Select Lawyer. . ."
                      options={options}
                    />
                    &nbsp;
                    <div className="form-group">
                      <input
                        type="textarea"
                        className={classnames("form-control form-control ", {
                          "is-invalid": errors.email
                        })}
                        placeholder="Email Address"
                        name="email"
                        value={this.state.email}
                        onChange={this.onChange}
                      />
                      {errors.email && (
                        <div className="invalid-feedback">{errors.email}</div>
                      )}
                    </div>
                    <div className="form-group">
                      <input
                        type="text"
                        className={classnames("form-control form-control-lg", {
                          "is-invalid": errors.password
                        })}
                        placeholder="Review Title"
                        name="password"
                        value={this.state.password}
                        onChange={this.onChange}
                      />
                      {errors.password && (
                        <div className="invalid-feedback">{errors.password}</div>
                      )}
                    </div>
                    <div className="form-group">
                      <input
                        type="text"
                        className={classnames("form-control form-control-lg", {
                          "is-invalid": errors.password2
                        })}
                        placeholder="Description"
                        name="password2"
                        value={this.state.password2}
                        onChange={this.onChange}
                      />
                      {errors.password && (
                        <div className="invalid-feedback">{errors.password2}</div>
                      )}
                    </div>
                    <div className="lead text-center">
                      <StarRatings 
                        rating={this.state.rating}
                        starRatedColor="blue"
                        changeRating={this.changeRating}
                        numberOfStars={6}
                        name='rating'
                      />
                    </div>
                    <input type="submit" className="btn btn-info btn-block mt-4" />
                  </form>
                </div>
              </div>
            </div>
          </div>


        </div>

      </div>
    );
  }
}

Dashboard.propTypes = {
  addreview: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth
});

export default connect(
  mapStateToProps
  ,
  { addreview }
)(Dashboard);



