import { combineReducers } from "redux";
import authReducer from "./authReducer";
import errorReducer from "./errorReducer";
import caseReducer from "./caseReducer";


export default combineReducers({
  auth: authReducer,
  errors: errorReducer,
  case: caseReducer,
});



