import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from "classnames";
import { addcase, getCurrentCase } from '../../actions/authActions';
import ReactLoading from 'react-loading';

const authActions = ({ navigation, route }) => {
    const [masterDataSource, setMasterDataSource] = useState([]);
    const [TableContant, setTableContant] = useState([]);
    const loading = false;
    const getDetails = (types) => {
        if (route) {
            switch (types) {
                case "title":
                    return route.params.title
                case "type":
                    return route.params.type
                case "code":
                    return route.params.code
                case "client":
                    return route.params.client
                case "court":
                    return route.params.court
                case "hearingDate":
                    return route.params.hearingDate
                case "description":
                    return route.params.description
                default:
                    return null

            }
        }
        return ""
    }
    const [title, setTitle] = useState(getDetails("title"))
    const [type, setType] = useState(getDetails("type"))
    const [code, setCode] = useState(getDetails("code"))
    const [client, setClient] = useState(getDetails("client"))
    const [court, setCourt] = useState(getDetails("court"))
    const [hearingDate, setHearingDate] = useState(getDetails("hearingDate"))
    const [description, setDescription] = useState(getDetails("description"))
    const submitData = () => {
        console.log("In submit data");
        if (title === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Name`)
        } else if (type === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Email`)
        } else if (code === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Password`)
        } else if (client === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Email`)
        } else if (court === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Password`)
        } else if (hearingDate === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Email`)
        } else if (description === "") {
            console.log("Error!");
            //            Alert.alert(`Please Enter Password`)
        } else {
            fetch("http://localhost:5000/api/cases/send-Case", {
                method: "post",
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    title,
                    type,
                    code,
                    client,
                    court,
                    hearingDate,
                    description,
                })
            })
                .then(res => res.json())
                .then(data => {
                    //                   Alert.alert(`${data.name} is saved successfuly`)
                    navigation.navigate("Login")
                })
                .catch(err => {
                    //                    Alert.alert("someting went wrong")
                })
        }
    }
    const getData = () => {
        useEffect(() => {
            fetch('http://localhost:5000/api/cases/getcases')
                .then((res) => res.json())
                .then((responseJson) => {
                    setMasterDataSource(responseJson);
                    console.log(responseJson);
                })
                .catch((error) => {
                    console.error(error);
                });
        }, []);
    }
    const TableData = () => {
        getData();
        const { studentData } = { masterDataSource };
        (!studentData) ? (
            setTableContant(null)
        ) : setTableContant(studentData.data ? studentData.data.map(
            el =>
                <tr key={el._id} >
                    <th scope="row">{studentData.indexOf(el) + 1}</th>
                    <td>{el.title ? el.title : "-"}</td>
                    <td>{el.type ? el.type : "-"}</td>
                    <td>{el.code ? el.code : "-"}</td>
                    <td>{el.client ? el.client : "-"}</td>
                    <td>{el.court ? el.court : "-"}</td>
                    <td>{el.hearingDate ? el.hearingDate : "-"}</td>
                    <td>{el.description ? el.description : "-"}</td>
                    <td style={{ cursor: 'pointer', color: '#00a4eb' }}
                        onClick=
                        {() => this.onDelete(el.id)}
                    >
                        Delete
                    </td>
                    <td style={{ cursor: 'pointer', color: '#00a4eb' }}
                        onClick=
                        {() => this.onDelete(el.id)}
                    >
                        Update
                    </td>

                </tr>
        ) : null
        )
    }
    //const { errors } = this.state;
    return (
        <div className="mid container">
            {/* <h1>{this.state.name}</h1> */}
            <br />
            <form onSubmit={submitData()}>
                <div className="row">
                    <div className="col">
                        <label htmlFor="name">Title</label>
                        <input type="text" id="title" placeholder="Name"
                            className={classnames("form-control", {
                                //                                "is-invalid": errors.name
                            })}
                            name="title"
                            //value={this.state.title}
                            onChange={text => setTitle(text)}
                        />
                        {/* {errors.title && (
                            <div className="invalid-tooltip">{errors.title}</div>
                        )} */}
                    </div>
                    <div className="col">
                        <label htmlFor="email">Type</label>
                        <input type="email" id="email" aria-describedby="emailHelp" placeholder="Enter email"
                            className={classnames("form-control", {
                                //                                "is-invalid": errors.email
                            })}
                            name="email"
                            //value={this.state.title}
                            onChange={text => setType(text)}
                        />
                        {/* {errors.email && (
                            <div className="invalid-tooltip">{errors.email}</div>
                        )} */}
                    </div>
                    <div className="col">
                        <label htmlFor="id">Code</label>
                        <input type="number" id="id" placeholder="ID"
                            className={classnames("form-control", {
                                //                                "is-invalid": errors.id
                            })}
                            name="id"
                            //value={this.state.title}
                            onChange={text => setCode(text)}
                        />
                        {/* {errors.id && (
                            <div className="invalid-tooltip">{errors.id}</div>
                        )} */}
                    </div>
                    <div className="col">
                        <label htmlFor="block">Client</label>
                        <input type="text" id="block" placeholder="Block"
                            className={classnames("form-control", {
                                // "is-invalid": errors.block
                            })}
                            name="block"
                            //value={this.state.title}
                            onChange={text => setClient(text)}
                        />
                        {/* {errors.block && (
                            <div className="invalid-tooltip">{errors.block}</div>
                        )} */}
                    </div>
                    <div className="col">
                        <label htmlFor="room">Court</label>
                        <input type="text" id="room" placeholder="Room No."
                            className={classnames("form-control", {
                                //                                "is-invalid": errors.room
                            })}
                            name="room"
                            //value={this.state.title}
                            onChange={text => setCourt(text)}
                        />
                        {/* {errors.room && (
                            <div className="invalid-tooltip">{errors.room}</div>
                        )} */}
                    </div>
                    <div className="col">
                        <label htmlFor="room">Hearing Date</label>
                        <input type="text" id="room" placeholder="Room No."
                            className={classnames("form-control", {
                                //                                "is-invalid": errors.room
                            })}
                            name="room"
                            //value={this.state.title}
                            onChange={text => setHearingDate(text)}
                        />
                        {/* {errors.room && (
                            <div className="invalid-tooltip">{errors.room}</div>
                        )} */}
                    </div>
                    <div className="col">
                        <label htmlFor="room">Description</label>
                        <input type="text" id="room" placeholder="Room No."
                            className={classnames("form-control", {
                                //                                "is-invalid": errors.room
                            })}
                            name="room"
                            //value={this.state.title}
                            onChange={text => setDescription(text)}
                        />
                        {/* {errors.room && (
                            <div className="invalid-tooltip">{errors.room}</div>
                        )} */}
                    </div>
                    <div className="col-auto" >
                        <button type="submit" style={{ verticalAlign: '-39px' }} className="btn btn-primary">Add</button>
                    </div>
                </div>
            </form>
            <div style={{ marginTop: '50px', overflow: 'scroll', maxHeight: 800 }}>
                {!loading ? <table className="table table-striped table-hover">
                    <thead className="thead-dark">
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Title</th>
                            <th scope="col">Type</th>
                            <th scope="col">Code</th>
                            <th scope="col">Client</th>
                            <th scope="col">Court</th>
                            <th scope="col">Hearing Date</th>
                            <th scope="col">Description</th>
                            <th scope="col">Delete?</th>
                            <th scope="col">Update?</th>
                        </tr>
                    </thead>
                    <tbody>
                        {TableContant}
                    </tbody>
                </table> : <div style={{ display: 'flex', justifyContent: 'center' }}><ReactLoading type="bars" color="#f56f42" /></div>}
                <button onclick={TableData}> Get Cases </button>
                <button onclick="TableData"> Button </button>
            </div>
        </div>
    );
}


authActions.propTypes = {
    addcase: PropTypes.func.isRequired,
    getCurrentCase: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
    errors: state.errors,
    authActions: state.authActions,
});

export default connect(mapStateToProps, { addcase, getCurrentCase })(authActions);

