import isEmpty from "../validation/is-empty";
import { SET_CURRENT_CASE, GET_CURRENT_CASE } from "../actions/types";

const initialState = {
  isAuthenticated: false,
  case: {},
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SET_CURRENT_CASE:
      return {
        ...state,
        isAuthenticated: !isEmpty(action.payload),
        user: action.payload
      };
    case GET_CURRENT_CASE:
      return {
        ...state,
        user: action.payload
      };
    default:
      return state;
  }
}


